var utils = {
    arr_split:function(index,data){       
        var result = [];
        for(var i=0,len=data.length;i<len;i+=index){
           result.push(data.slice(i,i+index));
        }
        return result;
    },
    foo:function(){
    	console.info('[sdm] ===== >  加载utils.js')
    }
};
module.exports = utils;


