<template>
  <div class='home marTop_50' v-cloak>
    <div class='container' style='min-height:400px;'>
      <div class='row'>
        <div class="panel panel-default" v-for='(item,index) in list'>
            <div class="panel-body">
                <div class='col-xs-12 col-sm-4'>
                  <div class='col-xs-6 col-sm-12 sdm_date sdm_grey pad_0'>
                    <i class='fa fa-calendar-minus-o'> </i>
                    &nbsp;{{item.date}}               
                    <!-- 2017/04/28 -->
                  </div>
                  <div class='col-xs-6 col-sm-12 sdm_comment-o sdm_grey cursor pad_0'>
                    <i class='fa fa-comment-o'> </i>
                    &nbsp;{{item.commentNum}}
                    <!-- 24 Comments -->
                  </div>
                </div>
                <div class='col-xs-12 col-sm-8'>
                  <h2 class='sdm_title cursor marTop_0'>{{item.title}}</h2>
                  <h5><i class='fa fa-cloud sdm_grey'>&nbsp;&nbsp;</i>{{item.subtitle}}</h5>
                  <div class='sdm_content'>
                    <p class='omit'>
                      {{item.content}}
                    </p>
                  </div>
                </div>
            </div>
        </div>
        <ul class="pagination" style='margin-left:30px;'>
            <li @click='page_back' v-if='this.curPage >0 && this.curPage !=1' class='cursor'><a>&laquo;</a></li>
            <li class='cursor' v-for='(item,index) in pages'>
              <a @click='page_click(index)' :class='{active : curPage == (index+1)}'>{{index+1}}</a>
            </li>
            <li @click='page_forward' v-if='this.curPage < this.pages.length' class='cursor'><a>&raquo;</a></li>
        </ul>
      </div>
    </div>

    <footer>
      <div class='col-xs-12 footer'>
        <div class='col-xs-12 col-sm-4'>
          <div @click='show_info("qq")' id='qq' class='box cursor'>
            <div class='box_1'>
              <i class='fa fa-qq fa-2x'>&nbsp;</i>
              <div class='fontb' style='color:#000;'>499289113</div>
            </div>
          </div>
        </div>
        <div class='col-xs-12 col-sm-4'>
          <div @click='show_info("wx")' id='wx' class='box cursor'>
            <div class='box_1'>
              <i class='fa fa-weixin fa-2x'>&nbsp;</i>
              <div class='fontb' style='color:#000;'>15021209145</div>
            </div>
          </div>
        </div>
        <div class='col-xs-12 col-sm-4'>
          <div @click='show_info("email")' id='email' class='box cursor'>
            <div class='box_1'>
              <i class='fa fa-envelope-square fa-2x'>&nbsp;</i>
              <div class='fontb' style='color:#000;'>499289113@qq.com</div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import u from '@/assets/js/utils.js'
import da from '@/assets/js/data.js'

export default {
  data () {
      return {
        msg: 'home Vue',
        name: 'Reborn',
        list:[],                      //list's data
        pages:[],
        curPage:1,
      }
    },
  components: {

  },
  mounted() {
    this.loadData(1);
  },
  methods:{
    foo() {
      console.log(jQuery.fn.jquery)
    },
    loadData(index){
      if (index <= 0) {
        return
      };
      this.list = [];
      this.pages = [];
      console.info('[sdm] ===== >  加载第'+index+'页数据');
      var data = da.test;
      // console.log(JSON.stringify(data))
      if (data && data.length > 0) {
        var new_arr = u.arr_split(5,data);
        var page = Math.ceil(data.length/5)
        for (var i = 0; i < page; i++) {
          this.pages.push(i);
        };
        if (index > page) {
          return
        };
        for (var i = 0; i < new_arr[index-1].length; i++) {
          this.list.push(new_arr[index-1][i]);
        };
          };
      // this.$http.get('/api/test').then(function(response){
      //     // 响应成功回调
      //     var data = response.body.data;
      //     // console.log(JSON.stringify(data))
      //     if (data && data.length > 0) {
      //       var new_arr = u.arr_split(5,data);
      //       var page = Math.ceil(data.length/5)
      //       for (var i = 0; i < page; i++) {
      //         this.pages.push(i);
      //       };
      //       if (index > page) {
      //         return
      //       };
      //       for (var i = 0; i < new_arr[index-1].length; i++) {
      //         this.list.push(new_arr[index-1][i]);
      //       };
      //     };
      // }, function(response){
      //     // 响应错误回调
      //     console.log('error');
      // });

    },
    page_click(index){
      this.curPage = index+1;
      this.loadData(this.curPage);
    },
    page_back(){
      console.log(this.curPage)
      this.loadData(this.curPage - 1);
      this.curPage --;
    },
    page_forward(){
      console.log(this.curPage)
      this.loadData(this.curPage + 1);
      this.curPage ++;
    },
    show_info(obj){
      if(null == obj){
        obj = '';
        return
      }
      var id = "#"+obj;
      var _this = $(id).find('.box_1');
      if (_this.hasClass('move_up')) {
          _this.removeClass('move_up');
          _this.addClass('move_down');
      } else{
          _this.addClass('move_up');
          _this.removeClass('move_down');
      };
    },
  }
}
</script>

<style scoped>
.pagination .active{
  color: #000;
  font-weight: bolder;
}
.box{
    padding: 10px;
    /*width: 100px;*/
     height: 41px; 
    line-height: 30px;
    overflow: hidden;
    text-align: center;
    /*display: inline-block;*/
}
.box_1{
  position: relative;
  transform:translate3d(0,0,0);
  -webkit-transform:translate3d(0,0,0);
  -webkit-backface-visibility:hidden;
  backface-visibility:hidden;
}
.move_up{
  -webkit-transform:translateY(-42px);
  transform: translateY(-42px);
  animation:move_up 300ms ease-in-out;
  -webkit-animation:move_up 300ms ease-in-out;
}
.move_down{
  -webkit-transform:translateY(0px);
  transform: translateY(0px);
  animation:move_down 300ms ease-in-out;
  -webkit-animation:move_down 300ms ease-in-out;
}
@-webkit-keyframes move_up{from{-webkit-transform:translateY(0px);transform:translateY(0px);}to{-webkit-transform:translateY(-42px);transform:translateY(-42px);}}@keyframes move_up{from{-webkit-transform:translateY(0px);transform:translateY(0px);}to{-webkit-transform:translateY(-42px);transform:translateY(-42px);}}
@-webkit-keyframes move_down{from{-webkit-transform:translateY(-42px);transform:translateY(-42px);}to{-webkit-transform:translateY(0px);transform:translateY(0px);}}@keyframes move_down{from{-webkit-transform:translateY(-42px);transform:translateY(-42px);}to{-webkit-transform:translateY(0px);transform:translateY(0px);}}
</style>



