<template>
  <div id="app">
    <div class="bgcolor marTop_75">
      <div class='container'>
        <div class="row">
          <nav class="navbar navbar-default noborder" role="navigation" style='background: transparent;'>
              <div class="container-fluid noborder">
              <div class="navbar-header noborder">
                  <button type="button" class="navbar-toggle" data-toggle="collapse"
                          data-target="#example-navbar-collapse">
                      <span class="sr-only">切换导航</span>
                      <span class="icon-bar"></span>
                      <span class="icon-bar"></span>
                      <span class="icon-bar"></span>
                  </button>
                  <div class="navbar-brand" style='padding:0px 15px;'>
                    <img @click='about' class='img_h' src="./assets/img/Mario.png" />
                  </div>
              </div>
              <div class="collapse navbar-collapse noborder" id="example-navbar-collapse">
                  <ul id='sdm_bar' class="nav navbar-nav">
                      <li class='navbar-nav_li active'>
                        <router-link to="/">Home</router-link>
                      </li>
                      <li class='navbar-nav_li'>
                        <router-link to="/about">About</router-link>
                      </li>
                      <li class='navbar-nav_li'>
                        <router-link to="/news">News</router-link>
                      </li>
                      <li class='navbar-nav_li'>
                        <router-link to="/pic">Pic</router-link>
                      </li>
                  </ul>
              </div>
              </div>
          </nav> 
        </div>
      </div>
        <!-- <img src="./assets/img/logo.png"> -->
        <div >
          <router-view></router-view> 
        </div>  
    </div>
  </div>
</template>

<script>

export default {
  name: 'app',
  data () {
      return {
        msg: 'app Vue',
        name: 'Reborn',
      }
    },
  components: {

  },
  mounted() {
    this.bar_click();
    this.init();

  },
  methods:{
    foo() {
      console.log('foo')
    },
    init(){     
      var flag = window.location.hash.substring(2);
      // console.log(flag);
      if (flag == 'home') {
        this.init_bar(0);
        return
      };
      if (flag == 'about') {
        this.init_bar(1);
        return
      };
      if (flag == 'news') {
        this.init_bar(2);
        return
      };
      if (flag == 'pic') {
        this.init_bar(3);
        return
      };
    },
    init_bar(index){
      $('#sdm_bar > li').removeClass('active');
      $('#sdm_bar > li').eq(index).addClass('active');
    },
    bar_click(){
      $('#sdm_bar > li').on('click',function(){
        var _this = $(this);
        var parent = _this.parent();
        parent.find('li').removeClass('active');
        _this.addClass('active');
      });
    },
    about(){
      this.init_bar(1);
      this.$router.push({path:'/about'});
    }
  }
}
</script>

<style>
/*@import './assets/css/animate.min.css';*/
/*@import './assets/css/font-awesome.min.css';*/
@import './assets/css/sdm.css';
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.bgcolor{
  background: #F6F6F6;
}
.img_h{
  width: 50px;
  cursor: pointer;
}
.active{
  font-weight: bolder;
  color: #fff;
  background: transparent !important;
}
@media screen and (min-width: 768px){
  .marTop_75{
    margin-top: 75px;
  }
}
/*.container {
    padding: 0 !important;
    width: 100% !important;
}*/

</style>



